













































































































































































































































































































































































































































































































































































































































































































































































































.word-table {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(0, 1fr));
    &.designtimemode {
        > .word-table-cell {
            position: relative;
            border-right: 1px solid #000;
            border-bottom: 1px solid #000;
            > .cell-content-wrapper {
                // height: 100%;
                // width: 100%;
                // overflow: hidden;
            }
            > .quick-insert-column {
                width: 20px;
                position: absolute;
                left: -11px;
                top: 0;
                bottom: -1px;
                > div {
                    margin-left: auto;
                    margin-right: auto;
                    opacity: 0;
                    transition: opacity 0.2s;
                }
                > .circle-plus {
                    height: 20px;
                    width: 20px;
                    position: absolute;
                    top: -20px;
                    border: 1px solid #3e6db5;
                    border-radius: 10px;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    cursor: pointer;
                    .icon.plus {
                        height: 100%;
                        width: 100%;
                        position: relative;
                        &:before {
                            content: "";
                            position: absolute;
                            width: 12px;
                            height: 1px;
                            background-color: #3e6db5;
                            left: 50%;
                            margin-left: -6px;
                            top: 50%;
                            margin-top: -0.5px;
                        }
                        &:after {
                            content: "";
                            position: absolute;
                            width: 12px;
                            height: 1px;
                            background-color: #3e6db5;
                            transform: rotate(90deg);
                            left: 50%;
                            margin-left: -6px;
                            top: 50%;
                            margin-top: -0.5px;
                        }
                    }
                }
                > .line {
                    width: 5px;
                    height: 100%;
                    border-left: 1px solid #3e6db5;
                    border-right: 1px solid #3e6db5;
                    cursor: w-resize;
                    background-color: #fff;
                    position: relative;
                    z-index: 1;
                }
            }
            > .quick-insert-last-column {
                width: 20px;
                position: absolute;
                right: -10px;
                top: 0;
                bottom: -1px;
                > div {
                    margin-left: auto;
                    margin-right: auto;
                    opacity: 0;
                    transition: opacity 0.2s;
                }
                > .circle-plus {
                    height: 20px;
                    width: 20px;
                    position: absolute;
                    top: -20px;
                    border: 1px solid #3e6db5;
                    border-radius: 10px;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    cursor: pointer;
                    .icon.plus {
                        height: 100%;
                        width: 100%;
                        position: relative;
                        &:before {
                            content: "";
                            position: absolute;
                            width: 12px;
                            height: 1px;
                            background-color: #3e6db5;
                            left: 50%;
                            margin-left: -6px;
                            top: 50%;
                            margin-top: -0.5px;
                        }
                        &:after {
                            content: "";
                            position: absolute;
                            width: 12px;
                            height: 1px;
                            background-color: #3e6db5;
                            transform: rotate(90deg);
                            left: 50%;
                            margin-left: -6px;
                            top: 50%;
                            margin-top: -0.5px;
                        }
                    }
                }
                > .line {
                    width: 5px;
                    height: 100%;
                    border-left: 1px solid #3e6db5;
                    border-right: 1px solid #3e6db5;
                    cursor: w-resize;
                    background-color: #fff;
                    position: relative;
                    z-index: 1;
                }
            }
            > .quick-insert-row {
                height: 20px;
                display: flex;
                align-items: center;
                position: absolute;
                bottom: -11px;
                left: 0;
                right: 0;
                > div {
                    opacity: 0;
                    transition: opacity 0.2s;
                }
                > .circle-plus {
                    height: 20px;
                    width: 20px;
                    position: absolute;
                    left: -20px;
                    border: 1px solid #3e6db5;
                    border-radius: 10px;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    cursor: pointer;
                    .icon.plus {
                        height: 100%;
                        width: 100%;
                        position: relative;
                        &:before {
                            content: "";
                            position: absolute;
                            width: 13px;
                            height: 1px;
                            background-color: #3e6db5;
                            left: 50%;
                            margin-left: -6.5px;
                            top: 50%;
                            margin-top: -0.5px;
                        }
                        &:after {
                            content: "";
                            position: absolute;
                            width: 13px;
                            height: 1px;
                            background-color: #3e6db5;
                            transform: rotate(90deg);
                            left: 50%;
                            margin-left: -6.5px;
                            top: 50%;
                            margin-top: -0.5px;
                        }
                    }
                }
                > .line {
                    height: 5px;
                    border-top: 1px solid #3e6db5;
                    border-bottom: 1px solid #3e6db5;
                    width: 100%;
                    cursor: s-resize;
                    background-color: #fff;
                }
            }
            &.activeinsertrow {
                > .quick-insert-row {
                    > div {
                        opacity: 1;
                    }
                }
            }
            &.activeinsertcolumn {
                > .quick-insert-column {
                    > div {
                        opacity: 1;
                    }
                }
            }
            &.activemergecell {
                background-color: rgb(198, 198, 198);
            }
            &.hastopborder {
                border-top: 1px solid #000;
            }
            &.hasleftborder {
                border-left: 1px solid #000;
            }
            &.activeinsertcolumnlast {
                > .quick-insert-last-column {
                    > div {
                        opacity: 1;
                    }
                }
            }
        }
    }
}
